import React, { useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import { auth, googleProvider } from '../firebase';
import { signInWithEmailAndPassword, signInWithPopup } from 'firebase/auth';
import '../styles.css';

const SignIn = () => {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const navigate = useNavigate();

  const handleSignIn = async (e) => {
    e.preventDefault();
    try {
      await signInWithEmailAndPassword(auth, email, password);
      navigate('/');
    } catch (error) {
      alert(error.message);
    }
  };

  const handleGoogleSignIn = async () => {
    try {
      await signInWithPopup(auth, googleProvider);
      navigate('/');
    } catch (error) {
      alert(error.message);
    }
  };

  return (
    <div className="auth-container">
      <div className="logo-container">
        <span className="logo-text">Recetar</span>
      </div>
      <h2>Iniciar sesin</h2>
      <form onSubmit={handleSignIn}>
        <input
          type="email"
          placeholder="Correo electrnico"
          value={email}
          onChange={(e) => setEmail(e.target.value)}
          required
        />
        <input
          type="password"
          placeholder="Contrasea"
          value={password}
          onChange={(e) => setPassword(e.target.value)}
          required
        />
        <button type="submit">Iniciar sesin</button>
      </form>
      <button className="google-button" onClick={handleGoogleSignIn}>
        Iniciar sesin con Google
      </button>
      <p>
        No tienes una cuenta? <Link to="/signup">Regstrate aqu</Link>
      </p>
    </div>
  );
};

export default SignIn;
